import { useEffect, useCallback } from 'react'
import { useMetaMask } from 'metamask-react'

import styles from './styles/components/Home.module.scss'

const METAMASK_STATUS = {
  INITIALIZING: 'initializing',
  UNAVAILABLE: 'unavailable',
  NOT_CONNECTED: 'notConnected',
  CONNECTING: 'connecting',
  CONNECTED: 'connected',
}

const App = () => {
  const { status, connect, account } = useMetaMask()

  const getAngles = (clientX, clientY) => {
    const menu = document.querySelector(`.${styles.home}`)
    const menuRect = menu.getBoundingClientRect()
    const { x, y, width, height } = menuRect
    const dx = clientX - (x + 0.5 * width)
    const dy = clientY - (y + 0.5 * height)
    return { dx, dy }
  }

  const setAngles = useCallback(({ clientX, clientY }) => {
    const { dx, dy } = getAngles(clientX, clientY)
    const menu = document.querySelector(`.${styles.home}`)
    menu?.attributeStyleMap?.set('--x', `${dy / 20}deg`)
    menu?.attributeStyleMap?.set('--y', `${dx / 20}deg`)
  }, [])

  useEffect(() => {
    window.addEventListener('mousemove', setAngles)

    return () => window.removeEventListener('mousemove', setAngles)
  }, [setAngles])

  useEffect(() => {
    if (status === METAMASK_STATUS.NOT_CONNECTED) {
      connect()
    }
  }, [status, connect])

  return (
    <div className={styles.home}>
      <div className={styles.home__spotify}>
        <iframe
          title="spotify"
          src="https://open.spotify.com/embed/track/1n1y2kFPISpF9WGD3JaFo5"
          width="100%"
          height="80"
          frameBorder="0"
          allowtransparency="true"
        />
      </div>
      <div className={styles.home__login_status}>
        {status === METAMASK_STATUS.INITIALIZING && (
          <div>Synchronisation with MetaMask ongoing...</div>
        )}
        {status === METAMASK_STATUS.UNAVAILABLE && (
          <div>MetaMask not available</div>
        )}
        {status === METAMASK_STATUS.CONNECTING && <div>Connecting...</div>}
        {status === METAMASK_STATUS.CONNECTED && (
          <>
            <div>Connected:</div>
            <div>{account}</div>
          </>
        )}
      </div>
      <div className={styles.home__menu_wrapper}>
        <ul className={styles.home__menu}>
          <li>
            <button
              type="button"
              onClick={() => window.open('https://opensea.io/0xHanson')}
            >
              @0xHanson
            </button>
          </li>
          <li>
            <button
              type="button"
              onClick={() => window.open('https://wozhidao.vercel.app/')}
            >
              WOZHIDAO dApp
            </button>
          </li>
          <li>
            <button
              type="button"
              onClick={() => window.open('https://eggtw.xyz/')}
            >
               | EggTW
            </button>
          </li>
        </ul>
      </div>
      <div className={styles.home__footer}> Created by 0xHanson</div>
    </div>
  )
}

export default App
